<template>
  <div class="home">
    <img src="../assets/shawn.png" alt="Shawn Logo">
    <HelloWorld msg="The Groomsmen Initiative"/>
    <button class="button is-danger" @click="logout">Logout</button><br><br>Designed by Shawn Shao Yang.
    <br>
  </div>
</template>

<script>
// @ is an alias to /src
import firebase from "firebase";
import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "home",
  components: {
    HelloWorld
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace("login");
        });
    }
  }
};
</script>
